import React from 'react';

const Checkbox = ({
  input,
  label,
  type,
  meta: { touched, error, warning }
}) => (
  <div>
    <input {...input} placeholder={label} type={type} />
    <label> {label}</label>
    {touched &&
      ((error && <div>{error}</div>) ||
        (warning && <div>{warning}</div>))}
  </div>
)

export default Checkbox;
